@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);

.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 30px;

  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.app main {
  margin-left: 30px;
}

.titleForm {
  color: #f05a5b
}

@media (max-width: 1000px) {
  .app {
    flex-direction: column;
    
  }

  .app main {
    margin-left: 0;
    margin-top: 30px;
    width: 100%;
  }

  .app aside {
    width: 100%;
  }
}

aside {
  width: 320px;
  background: #fff;
  box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.02);
  border-radius: 2px;
  padding: 30px 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.15);
}

aside strong {
  font-size: 20px;
  text-align: center;
  display: block;
  color: #333;
}

aside form {
  margin-top: 30px;
}

aside form .input-block + .input-block {
  margin-top: 20px;
}

aside form .input-group {
  margin-top: 20px;
  display: grid;
  grid-gap: 20px;
  gap: 20px;
  grid-template-columns: 1fr 1fr;
}

aside form .input-group .input-block {
  margin-top: 0;
}

aside form .input-block label {
  color: #acacac;
  font-size: 14px;
  font-weight: bold;
  display: block;
}

aside form .input-block input {
  width: 100%;
  height: 32px;
  font-size: 14px;
  color: #666;
  border: 0;
  border-bottom: 1px solid #eee;
}

aside form button[type="submit"] {
  width: 100%;
  border: 0;
  margin-top: 30px;
  background: #f05a5b;
  padding: 15px 20px;
  font-weight: bold;
  color: #fff;
  cursor: pointer;
  transition: background 0.5s;
  font-size: 14px;
}

aside form button[type="submit"]:hover {
  background: #f37c7c;
}

main {
  flex: 1 1;
}

main ul {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 20px;
  gap: 20px;
  list-style: none;
}

@media (max-width: 650px) {
  main ul {
    grid-template-columns: 1fr;
  }
}

* {
  padding: 0;
  margin: 0;
  outline: 0;
  box-sizing: border-box;
}

html, body, #root {
  height: 100%;
}

body{
  background-color: #f0e5e5;
}

body,
input,
button {
  font-family: Roboto, sans-serif;
}
li.dev-item {
  background: #fff;
  box-shadow: 0 0 14px 0 rgba(0, 0, 0, 0.02);
  border-radius: 2px;
  padding: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.15);
}

li.dev-item header {
  display: flex;
  flex-direction: row;
  align-items: center;
}

li.dev-item header img {
  width: 54px;
  height: 54px;
  border-radius: 50%;
}

/* li.dev-item header .user-info {
  margin-left: 10px;
} */

.user-info strong {
  display: block;
  font-size: 16px;
  color: #f05a5b;
  margin-bottom: 5px;;
}

.user-info span {
  font-size: 13px;
  color: #999;
  margin-top: 2px;
}

li.dev-item p {
  color: #666;
  font-size: 14px;
  line-height: 20px;
  margin: 0;
}

li.dev-item a {
  color: #8e4dff;
  font-size: 14px;
  text-decoration: none;
}

li.dev-item a:hover {
  color: #5a2ea6;
}

.react-datepicker-wrapper {
    display: inline-block;
    padding: 0;
    border: 0;
    width: 100% !important;
}
.filterContainer{
    position: absolute;
    top: 170px;
    right: 10px;
    background-color: #fff;
    height: 250px;
    width: 200px;
    z-index: 99999;
    padding: 10px;
    border: 1px solid #f05a5b;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.filterContainer p {
    text-align: center;
    font-size: 18px;
    color: #f05a5b;
    margin-bottom: 20px;
}

.checkContainer {
    display: flex;
}

.formControl {
    flex: 1 1;
}
.headerContainer {
    display: flex;
    padding: 10px;
    background: #fff;
    border-bottom: 1px solid hsl(360, 83%, 65%);
    align-content: center;
    align-items: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.15);
}

.logo{
    height: 80px;
    width: 80px;
    margin-right: 25px;
    margin-left: 25px;
}

.headerListContainer {
    margin-left: 25px;
    border-left: 1px solid #ccc;
    padding-left: 25px;
    height: 50px;
    display: flex;
    align-items: center;
}

.headerListContainer ul{
    list-style: none;
    display: flex;
}

.headerListContainer ul li{
    margin-right: 25px;
    color: #f05a5b;
    font-size: 18px;
    cursor: pointer;
}


.headerListContainer ul li:hover{
    text-decoration: underline;
}

a:-webkit-any-link {
    margin-right: 25px;
    color: #f05a5b;
    font-size: 18px;
    cursor: pointer;
    text-decoration:none;
}

