.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 60px 30px;

  display: flex;
  flex-direction: row;
  align-items: flex-start;
}

.app main {
  margin-left: 30px;
}

.titleForm {
  color: #f05a5b
}

@media (max-width: 1000px) {
  .app {
    flex-direction: column;
    
  }

  .app main {
    margin-left: 0;
    margin-top: 30px;
    width: 100%;
  }

  .app aside {
    width: 100%;
  }
}
